import { Link } from "gatsby";
import * as React from "react";
import { PropsWithChildren } from "react";

import { defaultLanguage } from "../../../nubarchiva/settings/translations";

export type PropsPagination = PropsWithChildren<{
  nextPagePath: string;
  numberOfPages: number;
  pageNumber: number;
  previousPagePath: string;
  categorySlug?: string | null | undefined;
  lang: string;
}>;

const Pagination: React.FC<PropsPagination> = (props: PropsPagination) => {
  const has_fivePagesOrMore: boolean = props.numberOfPages >= 5;
  const numbersPagination_render: React.ReactNode[] = [];
  if (props.numberOfPages < 2) {
    return <></>;
  }
  let numberStart: number = has_fivePagesOrMore && props.pageNumber > 2 ? props.pageNumber - 1 : 1;
  let numberStop: number = has_fivePagesOrMore ? props.pageNumber + 1 : props.numberOfPages;
  let lastNumberPrint = 0;
  if (numberStop < props.numberOfPages && numberStop < 3) {
    numberStop = 3;
  }
  if (has_fivePagesOrMore && numberStart > props.numberOfPages - 2) {
    numberStart = props.numberOfPages - 2;
  }
  const is_lastNumber = props.pageNumber !== props.numberOfPages && props.numberOfPages > 1;
  for (let numberPage = numberStart; numberPage <= numberStop; numberPage++) {
    lastNumberPrint = numberPage;
    if (numberPage === 1) {
      numbersPagination_render.push(
        <Link
          className={`a-pagination__item ${props.pageNumber === numberPage ? "a-pagination__item--active" : ""} `}
          key={numberPage}
          to={`/${props.lang === defaultLanguage ? "" : `${props.lang}/`}blog/${
            props.categorySlug ? `${props.categorySlug}/` : ""
          }`}
        >
          {numberPage}
        </Link>
      );
      continue;
    }
    numbersPagination_render.push(
      <Link
        className={`a-pagination__item ${props.pageNumber === numberPage ? "a-pagination__item--active" : ""} `}
        key={numberPage}
        to={`/${props.lang === defaultLanguage ? "" : `${props.lang}/`}blog/${
          props.categorySlug ? `${props.categorySlug}/` : ""
        }${numberPage}/`}
      >
        {numberPage}
      </Link>
    );
    if (numberPage === props.numberOfPages) {
      break;
    }
  }
  return (
    <div className={"o-pagination"}>
      <div className="c-pagination">
        {props.pageNumber > 1 && (
          <>
            <Link
              title={"Anterior"}
              rel={"prev nofollow"}
              className={"a-pagination__item"}
              to={`/${props.previousPagePath}`}
            >
              &#60;&#60;
            </Link>
            {numberStart !== 1 && (
              <>
                <Link
                  to={`/${props.lang === defaultLanguage ? "" : `${props.lang}/`}blog/${
                    props.categorySlug ? `${props.categorySlug}/` : ""
                  }`}
                  className={"a-pagination__item"}
                >
                  1
                </Link>
                <span className={"a-pagination__item"}>... </span>
              </>
            )}
          </>
        )}
        {numbersPagination_render}
        {has_fivePagesOrMore && is_lastNumber && (
          <>
            {/*{(props.numberOfPages !== props.pageNumber + 2 || !is_addEndDots) && <span>... </span>}*/}
            {lastNumberPrint !== props.numberOfPages - 1 && <span className={"a-pagination__item"}>... </span>}
            <Link
              className={`a-pagination__item ${
                props.pageNumber === props.numberOfPages ? "a-pagination__item--active" : ""
              } `}
              to={`/${props.lang === defaultLanguage ? "" : `${props.lang}/`}blog/${
                props.categorySlug ? `${props.categorySlug}/` : ""
              }${props.numberOfPages}/`}
            >
              {props.numberOfPages}
            </Link>
          </>
        )}
        {is_lastNumber && (
          <Link
            title={"Siguiente"}
            rel={"next nofollow"}
            className={"a-pagination__item"}
            to={`/${props.nextPagePath}/`}
          >
            &#62;&#62;
          </Link>
        )}
      </div>
    </div>
  );
};

export default Pagination;
