import * as React from "react";

import Card, { IPropsCard } from "../Card/Card";
import DoNotMountComponent from "../DoNotMountComponent/DoNotMountComponent";

type PropsCardList = {
  cardList: IPropsCard[] | null | undefined;
  twoColumns?: boolean;
  tagTitles?: "h2" | "h3" | "p" | "h4" | "h5" | "h6" | undefined;
  tagCards?: "article" | "div" | "section" | "aside" | "header" | "footer" | "nav" | "main" | undefined;
};

const CardList: React.FC<PropsCardList> = ({ cardList, tagTitles, tagCards, twoColumns }: PropsCardList) => {
  if (!cardList || !Array.isArray(cardList)) return <DoNotMountComponent sms={"faltan cards"} />;
  return (
    <div className={`m-card-list ${cardList.length % 2 === 0 || twoColumns ? "card-list--x2" : ""}`}>
      {cardList.map((card, index: number) => (
        <Card
          key={index}
          title={card.title}
          tagTitle={tagTitles}
          linkTo={card.linkTo}
          tagCard={tagCards}
          resume_html={card.resume_html}
        />
      ))}
    </div>
  );
};

export default CardList;
