import { Link } from "gatsby";
import * as React from "react";
import { useTranslation } from "react-i18next";

import { defaultLanguage } from "../../../nubarchiva/settings/translations";
import DoNotMountComponent from "../DoNotMountComponent/DoNotMountComponent";
import { ILink } from "../shared/ui/link";
import { getSlugWithLang } from "../shared/utils/getSlugWithLang";

const Categories: React.FC<{ categories: ILink[] }> = ({ categories }) => {
  if (!Array.isArray(categories) || categories.length < 1) return <DoNotMountComponent sms={"No hay categorías"} />;
  const { t, i18n } = useTranslation("global");
  const lang = i18n.language;

  return (
    <div className={"m-categories-list"}>
      <p className={"categories-list__title"}>{t("categories")}</p>

      <ul className={"categories-list__ul"}>
        {categories.map((item, index) => (
          <li key={index} className={"categories-list__item"}>
            <Link
              className={"categories-list__link"}
              activeClassName={"categories-list__link--active"}
              to={getSlugWithLang(`blog/${item.slug}`, lang)}
              title={`${t("filterBy")} ${item.text}`}
            >
              {item.text}
            </Link>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default Categories;
