import { Link, navigate } from "gatsby";
import * as React from "react";
import { PropsWithChildren } from "react";
import ReactHtmlParse from "react-html-parser";
import { useTranslation } from "react-i18next";

import { isotype_svg } from "../Company/logos_element";
import DoNotMountComponent from "../DoNotMountComponent/DoNotMountComponent";
import { getSlugWithLang } from "../shared/utils/getSlugWithLang";

export type IPropsCard = PropsWithChildren<{
  title: string | null | undefined;
  resume_html: string | null | undefined;
  linkTo?: string | null | undefined;
  tagTitle?: "h2" | "h3" | "p" | "h4" | "h5" | "h6" | undefined;
  tagCard?: "article" | "div" | "section" | "aside" | "header" | "footer" | "nav" | "main" | undefined;
}>;

const Card: React.FC<IPropsCard> = ({ title, resume_html, tagTitle, tagCard, linkTo }: IPropsCard) => {
  if (!title || !resume_html) return <DoNotMountComponent sms={"No tiene title o resume"} />;
  const { t, i18n } = useTranslation("global");
  const title_render = React.createElement(tagTitle || "p", { className: "card__title" }, title);
  const link = linkTo ? getSlugWithLang(`/blog/${linkTo}/`, i18n.language) : undefined;
  function goLink() {
    if (!linkTo) return;
    navigate(`${link}`);
  }

  const content_render = (
    <>
      {isotype_svg}
      {link ? <Link to={`${link}`}>{title_render}</Link> : title_render}
      <div className={"card__resume"}>{ReactHtmlParse(resume_html)}</div>
      {link && (
        <Link to={`${link}`} className={"card__read-more"}>
          {t("readme")} {">"}
        </Link>
      )}
    </>
  );

  const card_render = React.createElement(
    tagCard || "div",
    { className: `m-card ${link ? "m-card--clickable" : ""}`, onClick: goLink },
    content_render
  );
  return card_render;
};

export default Card;
