import { graphql, PageProps } from "gatsby";
import React from "react";

import { Language } from "../../../nubarchiva/settings/translations";
import BannerIcons from "../../modules/BannerIcons/BannerIcons";
import CardList from "../../modules/CardList/CardList";
import Categories from "../../modules/Categories/Categories";
import HeadLineBlog from "../../modules/HeadLineBlog/HeadLineBlog";
import Layout from "../../modules/Layout/Layout";
import Pagination from "../../modules/Pagination/Pagination";
import { SEO } from "../../modules/SEO/SEO";
import { ILink } from "../../modules/shared/ui/link";
import { getSlugWithLang } from "../../modules/shared/utils/getSlugWithLang";

const PageBlog = (props: PageProps<Queries.pageBlogQuery>) => {
  return (
    <Layout
      landingName={"blog"}
      lang={props.pageContext.lang}
      useLinks={[]}
      navbarLinksList={[...props.data.navbarLinks.nodes.map((item: any) => item.metas)]}
      legalLinks={props.data.allLegals?.nodes?.map((item) => item.childMarkdownRemark?.frontmatter) || []}
    >
      <div className="c-blog">
        <aside className={"blog__aside"}>
          <Categories categories={props.data.allCategory.nodes.map((item: ILink) => item.metas)} />
        </aside>
        <main className={"blog__main"}>
          <HeadLineBlog isIndexBlog={props.pageContext.isIndexBlog} title={props.data.category.metas.title} />
          <CardList
            cardList={[
              ...props.data.allFile.nodes
                .filter((node: any) => node.relativeDirectory.includes(props.pageContext.lang))
                .map((post: any, index: number) => {
                  return {
                    title: post.childMarkdownRemark.frontmatter.summary.title,
                    resume_html: `<p>${post.childMarkdownRemark.frontmatter.summary.description}</p>`,
                    linkTo: post.childMarkdownRemark.frontmatter.metas.slug,
                  };
                }),
            ]}
            twoColumns={true}
            tagCards={"article"}
            tagTitles={"h2"}
          />
          <Pagination
            nextPagePath={props.pageContext.nextPagePath}
            numberOfPages={props.pageContext.numberOfPages}
            pageNumber={props.pageContext.humanPageNumber}
            previousPagePath={props.pageContext.previousPagePath}
            categorySlug={props.data.category.metas.slug}
            lang={props.pageContext.lang}
          />
        </main>
      </div>
      <BannerIcons isRelative={true} />
    </Layout>
  );
};

export const Head = (props: PageProps<Queries.pageBlogQuery>) => (
  <SEO
    title={
      props.pageContext.isIndexBlog
        ? "Blog de nubarchiva: Transformando la Gestión de Archivos con Software Libre"
        : props.data.category?.metas?.title
    }
    description={
      props.pageContext.isIndexBlog
        ? "Explora nuestro blog para descubrir cómo nubarchiva utiliza el software libre para impulsar la innovación y la colaboración en la gestión de archivos. Conoce nuestras últimas noticias, actualizaciones y cómo puedes ser parte de nuestra comunidad."
        : `${props.data.category?.metas?.description}`
    }
    canonical={
      props.pageContext.isIndexBlog
        ? getSlugWithLang("blog", props.pageContext.lang)
        : getSlugWithLang(`blog/${props.data.category?.metas?.slug}`, props.pageContext.lang)
    }
    slug={props.pageContext.isIndexBlog ? "blog" : `blog/${props.data.category?.metas?.slug}`}
    lang={props.pageContext.lang as Language}
    hrefLangList={
      props.pageContext.isIndexBlog
        ? props.data.hrefLangsList?.nodes?.map((node) => ({
            hrefLang: node.lang,
            slug: "blog",
          }))
        : props.data.hrefLangsList?.nodes?.map((node) => ({
            hrefLang: node.lang,
            slug: `blog/${node.metas?.slug}`,
          }))
    }
  />
);
export default PageBlog;

export const query = graphql`
  query pageBlog(
    $skip: Int!
    $limit: Int!
    $uuid: String
    $category: [String]
    $settingsId: String
    $lang: String = "es_es"
  ) {
    category(id: { eq: $uuid }) {
      metas {
        slug
        title
        description
      }
    }
    allFile(
      filter: {
        sourceInstanceName: { eq: "blog" }
        ext: { eq: ".md" }
        childMarkdownRemark: { frontmatter: { category: { in: $category }, settings: { active: { eq: true } } } }
      }
      skip: $skip
      limit: $limit
      sort: { modifiedTime: DESC }
    ) {
      nodes {
        relativeDirectory
        childMarkdownRemark {
          frontmatter {
            metas {
              slug
            }
            description
            title
            category
            summary {
              title
              description
            }
          }
          id
        }
      }
    }
    navbarLinks: allPage(filter: { lang: { eq: $lang } }, sort: { settings: { order: ASC } }) {
      nodes {
        metas {
          name
          slug
        }
      }
    }
    allCategory(filter: { lang: { eq: $lang } }) {
      nodes {
        metas {
          title
          slug
          text: name
        }
      }
    }
    hrefLangsList: allCategory(filter: { settings: { id: { eq: $settingsId } } }) {
      nodes {
        metas {
          slug
        }
        lang
      }
    }
    allLegals: allFile(
      filter: { sourceInstanceName: { eq: "legal" }, relativeDirectory: { eq: $lang } }
      sort: { childMarkdownRemark: { frontmatter: { order: ASC } } }
    ) {
      nodes {
        childMarkdownRemark {
          frontmatter {
            slug
            name: title
          }
          id
        }
        relativeDirectory
      }
    }
  }
`;
