import { Link } from "gatsby";
import * as React from "react";
import { PropsWithChildren } from "react";
import { useTranslation } from "react-i18next";

import { getSlugWithLang } from "../shared/utils/getSlugWithLang";

type PropsHeadLineBlog = PropsWithChildren<{
  isIndexBlog?: boolean;
  title?: string;
}>;

const HeadLineBlog: React.FC<PropsHeadLineBlog> = ({ isIndexBlog, title }: PropsHeadLineBlog) => {
  const { t, i18n } = useTranslation("global");
  const lang = i18n.language;
  return (
    <div className={`m-head-line-blog`}>
      <h1 className={"a-title"}>{isIndexBlog ? t("blog.title") : `${title}`}</h1>
      {!isIndexBlog && (
        <ul className={"post__info"}>
          <li>
            <Link to={getSlugWithLang(`blog`, lang)}> {t("blog.name")} </Link>
          </li>
        </ul>
      )}
    </div>
  );
};

export default HeadLineBlog;
